"use client";

import Image from "next/image";
import { motion } from "framer-motion";

const portfolioSections = [
  { title: "Website Design", key: "website" },
  { title: "Dashboard Design", key: "dashboard" },
  { title: "Logo Design", key: "logo" },
  { title: "Product Branding", key: "branding" },
  { title: "Graphic Design", key: "graphic" },
  { title: "Game Design", key: "game" },
  { title: "Mobile App Design", key: "mobile" },
];

export default function Portfolio() {
  return (
    <div className="w-full py-16 px-4 md:px-10 text-white">
      <div className="max-w-7xl mx-auto space-y-24">
        {portfolioSections.map((section, index) => (
          <motion.section
            key={section.key}
            initial={{ opacity: 0, y: 40 }}
            whileInView={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.5, delay: index * 0.1 }}
            viewport={{ once: true }}
            className="bg-white/5 rounded-2xl p-6 md:p-10 shadow-md backdrop-blur-lg"
          >
            <h2 className="text-3xl font-bold mb-4 text-[#c2a4ff]">
              {section.title}
            </h2>
            <p className="mb-6 text-gray-300">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit. Here you can showcase projects, case studies, or images related to {section.title.toLowerCase()}.
            </p>
            <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 gap-4">
              {/* Replace these with actual images */}
              <div className="aspect-video bg-gray-800 rounded-lg" />
              <div className="aspect-video bg-gray-800 rounded-lg" />
              <div className="aspect-video bg-gray-800 rounded-lg" />
            </div>
          </motion.section>
        ))}
      </div>
    </div>
  );
}
